import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* Layout */
import Layout from '@/pages/layout/index'

/**
  vue router  https://router.vuejs.org/zh/api/#name

  declare type RouteConfig = {
    path: string;                               // /^(https?:|mailto:|tel:)/.test(path) 
    component?: Component;
    name?: string;                              //  <keep-alive>Tag
    components?: { [name: string]: Component }; // 
    redirect?: string | Location | Function;    //  `redirect:noredirect` 
    props?: boolean | Object | Function;
    alias?: string | Array<string>;
    children?: Array<RouteConfig>;              // 
    beforeEnter?: (to: Route, from: Route, next: Function) => void;
    meta?: any;

    // 2.6.0+
    caseSensitive?: boolean; // (false)
    pathToRegexpOptions?: Object; // 

    // vue
    //  alonso-admin-template 
    hidden: true                   // default: false, true 
    alwaysShow: true               // default: false true false,
    meta : {
      title: 'title'               // tag
      icon: 'svg-name'             // icon svg
      src: 'http://www.baidu.com'  //  iframe 
    }
}
**/
export const constantRouterMap = [
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path*',
        component: () => import('@/pages/redirect/index')
      }
    ]
  },
  { path: '/login', component: () => import('@/pages/login/index'), hidden: true },
  {
    path: '/404',
    component: () => import('@/pages/errorPage/404'),
    name: 'Page404',
    hidden: false,
    meta: { title: 'page404', icon: 'about', noCache: true }
  },
  {
    path: '/iframe',
    component: Layout,
    redirect: '/index',
    hidden: true,
    children: [
      {
        path: 'index',
        name: 'ifrmae',
        component: () => import(/* webpackChunkName: "views" */ '@/pages/iframe/index'),
        meta: {
          title: 'ifrmae',
          icon: 'setting-selected'
        }
      }
    ]
  }
]

export default new Router({
  // mode: 'history', //
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

// 
export const asyncRouterMap = [
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    hidden: false,
    children: [{
      path: 'dashboard',
      name: 'Dashboard',
      component: () => import(/* webpackChunkName: "views" */ '@/views/dashboard/index'),
      meta: { title: 'dashboard', icon: 'el-icon-menu', noCache: true, roles: ['admin', 'editor'] }
    }]
  },
  {
    path: '/about',
    component: Layout,
    redirect: '/index',
    hidden: false,
    meta: { title: 'about', icon: 'about', noCache: true, roles: ['admin'] },
    children: [{
      path: 'index',
      name: 'about',
      component: () => import(/* webpackChunkName: "views" */ '@/views/About'),
      meta: { title: 'about', icon: 'about', noCache: true, roles: ['admin'] }
    }]
  },
  {
    path: 'external-link',
    component: Layout,
    children: [
      {
        path: 'http://www.baidu.com/',
        meta: {
          title: 'externalLink',
          icon: 'setting-selected'
        }
      }
    ]
  },
  {
    path: '/third-site',
    component: Layout,
    redirect: '/163',
    meta: {
      title: 'thirdSite',
      icon: 'group'
    },
    children: [
      {
        path: 'gitee',
        name: 'gitee',
        component: () => import(/* webpackChunkName: "thirdSite" */ '@/pages/iframe/index'),
        meta: {
          src: 'https://www.gitee.com', title: 'gitee'
        }
      },
      {
        path: '163',
        name: '163',
        component: () => import(/* webpackChunkName: "thirdSite" */ '@/pages/iframe/index'),
        meta: {
          src: 'https://www.163.com', title: 'wangyi'
        }
      },
      {
        path: 'baidu',
        name: 'baidu',
        component: () => import(/* webpackChunkName: "thirdSite" */ '@/pages/iframe/index'),
        meta: {
          src: 'https://www.baidu.com#', title: 'baidu'
        }
      },
      {
        path: 'jingdong',
        name: 'jingdong',
        component: () => import(/* webpackChunkName: "thirdSite" */ '@/pages/iframe/index'),
        meta: {
          src: 'https://www.jd.com', title: 'jingdong'
        }
      }
    ]
  },

  { path: '*', redirect: '/404', hidden: true }
]
